import React from "react"

import "./Navbar.css"

const Navbar = () => {
  const [portrait, setPortrait] = React.useState(false)
  const [slide, setSlide] = React.useState(false)
  const [color, setColor] = React.useState(false)

  const check = () => {
    if (window.innerWidth > window.innerHeight) {
      setPortrait(false)
    } else if (window.innerWidth < window.innerHeight) {
      setPortrait(true)
    }
  }

  const checkScroll = () => {
    if(window.scrollY > 3) {
      setColor(true);
    } else if(window.scrollY < 3) {
      setColor(false);
    }
  }

  React.useEffect(() => {
    setInterval(check, 600)
    setInterval(checkScroll,200);
  }, [])
  return (
    <>
      <div className={color === true ? "NavigationBar color" : "NavigationBar"}>
        {portrait === false ? (
          <>
            <a href="#HomeWrapper">Strona Główna</a>
            <a href="#About">O mnie</a>
            <a href="#TechWrapper">Technologie</a>
            <a href="#ProjectsWrapper">Projekty</a>
          </>
        ) : (
          <>
            <div
              onClick={() => setSlide(!slide)}
              className={slide === true ? "Hamburger x" : "Hamburger"}
            >
              <section id={slide === true ? "cross" : "none"}></section>
              <section id={slide === true ? "inv" : "none"}></section>
              <section id={slide === true ? "cross2" : "none"}></section>
            </div>
          </>
        )}
      </div>
      {portrait === true && (
        <div className={slide === false ? "menuSlider slide" : "menuSlider"}>
          <a href="#HomeWrapper">Strona Główna</a>
          <a href="#About">O mnie</a>
          <a href="#TechWrapper">Technologie</a>
          <a href="#ProjectsWrapper">Projekty</a>
        </div>
      )}
    </>
  )
}

export default Navbar
