import React from "react"

import Bg1 from "./Bg1"
import Bg2 from "./Bg2"
import Bg3 from "./Bg3"

import "./Projects.css"
import "./Section.css"

const contentArray = [
  {
    title: "Projekt 1",
    src: 1,
  },
  {
    title: "Projekt 2",
    src: 2,
  },
  {
    title: "Projekt 3",
    src: 3,
  },
]

const Section = ({ children, title }) => {
  return (
    <Bg1>
      <h1 className="SectionTitle">{title}</h1>
    </Bg1>
  )
}

const Section2 = ({ children, title }) => {
  return (
    <Bg2>
      <h1 className="SectionTitle">{title}</h1>
    </Bg2>
  )
}

const Section3 = ({ children, title }) => {
  return (
    <Bg3>
      <h1 className="SectionTitle">{title}</h1>
    </Bg3>
  )
}


const Projects = () => {
  return (
    <div id="ProjectsWrapper" className="ProjectsWrapper">
      <h1 className="ProjectsTitle">Projekty</h1>
      <div className="MyProjects">
        <Section title="Czat Online" />
        <Section2 title="Zdrowie" />
        <Section3 title="Apka do nauki Phrasal Verbs" />
      </div>
    </div>
  )
}

export default Projects
