import React from "react"
import Img from "gatsby-image"
import { useStaticQuery, graphql } from "gatsby"

import "./Technologies.css"

const Technologies = () => {
  const data = useStaticQuery(graphql`
    {
      js: file(relativePath: { eq: "js.png" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      node: file(relativePath: { eq: "node.png" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      react: file(relativePath: { eq: "react.png" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      electron: file(relativePath: { eq: "electron.png" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      sass: file(relativePath: { eq: "sass.png" }) {
        childImageSharp {
          fluid(maxWidth: 250) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)
  return (
    <div id="TechWrapper" className="TechWrapper">
      <h1 className="TechTitle">Technologie</h1>
      <div className="TechGrid">
        <div className="TechContainer">
          <Img
            className="TechImg"
            fluid={data.electron.childImageSharp.fluid}
          />
          <h2 className="TechH2">Electron</h2>
        </div>
        <div className="TechContainer">
          <Img className="TechImg" fluid={data.node.childImageSharp.fluid} />
          <h2 className="TechH2">Node.js</h2>
        </div>
        <div className="TechContainer">
          <Img className="TechImg" fluid={data.js.childImageSharp.fluid} />
          <h2 className="TechH2">Javascript</h2>
        </div>
        <div className="TechContainer">
          <Img className="TechImg" fluid={data.react.childImageSharp.fluid} />
          <h2 className="TechH2">React</h2>
        </div>
        <div className="TechContainer">
          <Img className="TechImg" fluid={data.sass.childImageSharp.fluid} />
          <h2 className="TechH2">Sass</h2>
        </div>
      </div>
    </div>
  )
}

export default Technologies
